.app {
  background: url('./assets/backgrounds/night.jpg') no-repeat center;
  background-size: cover;
  height: 100vh;
  padding: 0 16px 16px 16px;
}

body {
  font-size: 14px;
  font-weight: 400;
  margin: 0;
  color: #fff;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}